.App {
    min-height: 100vh;
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);}
.title {
    display: flex;
    justify-content: center;
    padding:20px;
    font-weight: bold;
}
.search-form{
    min-height: 10vh;
    display:flex;
    justify-content: center;
    align-items: center;
}

.search-bar{
    width: 50%;
    border: none;
    border-radius: 25px;
    padding: 10px;
}

.search-button{
    margin-left: 1rem;
    padding:10px;
    border: none;
    background-color: lightcoral;
    color: #fff;
    font-weight: bold;
}

.recipes {
    display:flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    
}

.btn{
    margin-top:1rem;
}
