.recipe {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    background:hsl(0, 0%, 100%);
    align-items: center;
    min-width: 40%;
}

.image {
    border-radius: 30%;
    width:100px;
}